.forgotPassWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 120px;
}

.forgotPassContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 413px;
    height: 410px;
    background: #FFFFFF;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
}

h1{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;
    padding-top: 20px;
}
.textfield{
    width: 347px;
}
.text{
    padding-top: 25px;
    padding-bottom: 50px;
    width: 347px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    opacity: 0.5;
    text-align: left;
}
.question{
    padding-top: 30px;
    padding-bottom: 20px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: #000000;
    opacity: 0.5;
}
.try {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    text-decoration-line: underline;
    color: #366EFF;
}
