.profileWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 120px;
}

.profileContainer {
    width: 413px;
    height: 410px;
    background: #FFFFFF;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
}

.profileHeader {
    margin: 25px;
    font-family: sans-serif, 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    text-align: center;
    color: #000000;
}

.profileEmail {
    margin: 25px;
    font-family: sans-serif, 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    align-items: center;
    color: #000000;
    opacity: 0.5;
}

.profileBackTo {
position: absolute;
width: 150px;
height: 24px;
left: 168px;
top: 84px;
font-family:  sans-serif, 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 24px;
display: flex;
align-items: center;
color: #000000;
}

.profileBackTo span {
    margin-left: 10px;
}

.editableSpan {
    margin-top: 30px;
    margin-bottom: 31px;
}